/* 2019-03-13 loadingComponent 总览模块加载样式*/
.block-loading {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: transparent;
}
.block-loading svg {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
}
.block-loading svg path,
.block-loading svg rect {
  fill: #0696ff;
}
/* 重新加载样式 */
.block-update {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: transparent;
}
.block-update .text {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  width: 9em;
  height: 3em;
  text-align: center;
  cursor: pointer;
}
.block-update .text.underline {
  text-decoration: underline;
}
.block-update .text:hover {
  text-decoration: underline;
}
